@import '../../style/variables.scss';

.container {
  display: flex;
  justify-content: space-between;
  width: fit-content;
  max-width: 730px;
  max-height: 500px;

  @media (max-width: 580px) {
    flex-wrap: wrap;
    max-height: none;
  }
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.mainImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.thumbContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 50%;

  @media (max-width: 580px) {
    flex-direction: row;
    width: 100%;
  }
}

.thumbImageContainer {
  width: 80px;
  height: 80px;
  margin-right: 20px;
  margin-bottom: 40px;

  &:last-child {
    margin-bottom: 0;
  }
}

.mainImageContainer {
  max-width: 500px;
  height: 511px;
  margin-left: 40px;
}

.button {
  width: 100%;
  height: 100%;
}
